	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/3/bored-with-the-king/index.md absPath of file >>> MarkdownRemark","html":"<p>Apple, now the big player, has to take the hits that big players get:</p>\n<blockquote>\n<p>But that’s how news reporters increasingly are treating the state of the industry. The desire for the “Oh, how the mighty Apple has fallen” narrative is so strong that the narrative is simply being stated as fact, evidence to the contrary be damned. It’s reported as true simply because they want it to be true. They’re declaring “The King is dead; long live the King” not because the king has actually died or abdicated the throne, but because they’re bored with the king and want to write a new coronation story.</p>\n</blockquote>\n<p>Compared with <a href=\"http://kickingbear.com/blog/archives/305\">Apple’s real problems</a>, this is just an interesting but annoying distraction.</p>","frontmatter":{"title":"Bored with the king","date":"March 15, 2013","passthroughUrl":"http://daringfireball.net/2013/03/ceding_the_crown"}}},"pathContext":{"slug":"/blog/2013/3/bored-with-the-king/","previous":{"fields":{"slug":"/blog/2013/3/microsoft-is-dead/"},"frontmatter":{"title":"Microsoft is dead","passthroughUrl":"http://www.paulgraham.com/microsoft.html"}},"next":{"fields":{"slug":"/blog/2013/3/notes-from-an-early-screening-of-blade-runner/"},"frontmatter":{"title":"Notes from an early screening of Blade Runner","passthroughUrl":"http://i.imgur.com/htBlPKU.jpg"}}}}

